@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700&display=swap);
.Button{display:inline-block;height:34px;width:100px;font-size:var(--fs-md);border-radius:var(--button-br);color:var(--button-color);background-color:var(--button-primary-bg);cursor:pointer}.Button__container{display:flex;align-items:center;justify-content:center;width:100%;height:100%;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Button:hover{background-color:var(--button-hover-bg)}.Button--disabled,.Button--disabled:hover{color:var(--button-disabled-color);background-color:var(--button-disabled-bg)}

.Header{border:1px solid red;background-color:var(--white);position:fixed;top:0;right:0;width:100%;z-index:9999;box-shadow:0 2px 4px 0 rgba(0,0,0,0.02)}.Header__container{padding:0 15px;height:58px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;flex-wrap:nowrap}.Header__logo button{border:none;background-color:inherit}.Header__nav{display:flex;grid-gap:1rem;gap:1rem}


/* @import url('./reset.css');  */
/* drop styles button */

:root {
  /* Typography */
  --family: 'Nunito', sans-serif;
  --fs-sm: 12px;
  --fs-md: 16px;
  --fs-h1: 40px;

  --fw-normal: 400;
  --lh-sm: 14px;
  --lh-md: 26px;
  --lh-h1: 40px;

  /* Other */
  --yellow: #F4E041;
  --blue: #00BDD3;
  --light-grey: #F8F8F8;
  --bg-color: #E5E5E5;
  --white: #FFFFFF;
  --black: rgba(0, 0, 0, 0.87);
  
  /* UI */
  --button-color: rgba(0, 0, 0, 0.87);
  --button-primary-bg: #F4E041;
  --button-hover-bg: #FFE302;
  --button-disabled-bg: #B4B4B4;
  --button-disabled-color: rgba(255, 255, 255, 0.87);
  --button-br: 80px;
  --button-radii: 4px;
  
  --field-border-enabled: #D0CFCF;
  --field-radii: 4px;
  --field-helper-text: #7E7E7E;
  --field-border-error: rgba(203, 61, 64, 1);
  --error-color: #CB3D40;

  --card-br: 10px;
}

* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-family: var(--family);
  font-size: 16px;
  font-size: var(--fs-md);
  margin: 0;
  padding: 0;
}

body {
  background-color: #E5E5E5;
  background-color: var(--bg-color);
  /* min-width: 370px; */
  /* overflow-x: hidden; */
  overflow-y: hidden;
  /* overflow: scroll; */
}


